*{
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}
.header-bar{
  text-align: center;
}
main{
  height: 100%;
  padding:5px;
}
.title{
  color: #fff;
}
.form-error, .form-warning{
  font-weight: bold;
  color: red;
}

.login-form p{
  padding: 0;
  margin: 0;
}
.login-form p.login-example{
  margin: 5px 0;
  font-weight: bolder;
}

.german-title{
  /* background: -webkit-linear-gradient(110deg, #000 33%, rgba(0, 0, 0, 0) 33%), -webkit-linear-gradient(110deg, #dd0000 66%, #ffc608 66%);
  background: -o-linear-gradient(110deg, #000 33%, rgba(0, 0, 0, 0) 33%), -o-linear-gradient(110deg, #dd0000 66%, #ffc608 66%);
  background: -moz-linear-gradient(110deg, #000 33%, rgba(0, 0, 0, 0) 33%), -moz-linear-gradient(110deg, #dd0000 66%, #ffc608 66%);
  background: linear-gradient(110deg, #000 33%, rgba(0, 0, 0, 0) 33%), linear-gradient(110deg, #dd0000 66%, #ffc608 66%); */
  background-color: #ffc608;
  color: white;
  height: 100%;
  margin-top: 0;
  margin-bottom: 0px;
}
.czech-title{
background-color: blue;
  color: white;
  height: 100%;
  margin-top: 0;
  margin-bottom: 0px;
}

.home{
  max-width: 500px;
  background-color: white;
  padding-bottom: 10px;
  padding-left: 10px;
  margin-top: 10px;
  padding-top: 10px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  font-size: 20px;
}

.description{
  margin-top: 0;
}

h2{
  margin-top: 0;
}

body{
  background: url('../images/beer4.jpg') ; 
  background-color: rgba(255, 255, 255, 0.3);
  background-blend-mode: lighten;
  
}
.dashboard{
  max-width: 500px;
  height: 600px;
  margin: auto;
  background-color: white;
  border-radius: 5px;
  text-align: center;
  margin-top: 1%;
  color: black;
  padding: 10px;
}

.dashboard.scores{
  height:auto;
  margin-bottom: 5%;
}

.card{
    width: 100%;
		height: 300px;
		background-color: black;
		color: #fff;
		padding: 40px;
		border-radius: 2px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
  };

.line{
    width: 320px;
		height: 5px;
    margin-top: 10px;
  }
.card-text{
		margin-top: 15px;
		font-size: 15px;
		line-height: 1.6;
		color: white;
  }
.game-container{
  width: 100%;
  height:90%;
}
.user-input{
  display: block;
  margin-left:auto;
  margin-right: auto;
}
.select-subject{
  width: 200px;
  height: 40px;
  display: inline-block;
  margin: 0px auto;
}
.select-button{
  display: inline-block;
}
.select-label{
  display: block;
}

.dashboard-username-main{
  /* border-bottom: 1px solid black; */
  padding-bottom: 3px;
  border-radius: 5px;
  margin-bottom: 5px;
  background-color: #dd0000;
  color: #fff;
}

.dashboard-welcome{
  margin-top: 0;
  padding-top: 7px;
}

.dashboard-username{
  color: white;
  border-radius: 5px;
  border-bottom: 1px solid black;
  margin-bottom: 5px;
  background-color: #dd0000;
}
input{
  padding: 10px;
  width: 160px;
}

button{
 margin: 10px; 
 padding: 10px 20px;
 border-radius: 5px;
 background-color:	#505050;
 border: 2px solid black;
 color: white;
 width: 140px;
}

.submit-button{
  margin-bottom: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

ul {
  list-style-type: none;
  padding: 0;
}

h2, h3{
  font-weight: normal;
}

